@import url('https://fonts.googleapis.com/css2?family=Almarai:wght@700&display=swap');

body{
    font-family: 'Almarai', sans-serif;
}
.number{
    padding: 5px 10px;
    border-radius: 20%;
}
.text-green{
    background-color: rgb(36, 167, 10);
    color: white;
}
.text-red{
    background-color: rgb(233, 14, 14);
    color: white;
}
.text-blue{
    color: blue;
}
@media screen and (max-width:570px){
    .card .image{
        width: 90px;
        height: 90px;
    }
}